<script lang="coffeescript">var copy, deleteLine, dispatch, go, isLink, keyChange, toggleVisibility, valueChange;

import {
  tick,
  createEventDispatcher
} from 'svelte';

dispatch = createEventDispatcher();

export let item;

export let index;

import InlineInput from "../../../components/common/InlineInput.svelte";

import IconButton from "../../../components/common/IconButton.svelte";

keyChange = function(e) {
  dispatch("changeKey", {
    id: item.id,
    key: e.detail
  });
  return item.key = e.detail;
};

valueChange = function(e) {
  dispatch("changeValue", {
    id: item.id,
    value: e.detail
  });
  return item.value = e.detail;
};

deleteLine = function(e) {
  return dispatch("delete", item.id);
};

isLink = function(text) {
  var fileLink, httpLink, httpsLink, mailtoLink, telLink;
  text = text || "";
  httpLink = text.startsWith("http://");
  httpsLink = text.startsWith("https://");
  fileLink = text.startsWith("file://");
  mailtoLink = text.startsWith("mailto://");
  telLink = text.startsWith("tel://");
  return httpLink || httpsLink || fileLink || mailtoLink || telLink;
};

copy = function(e) {
  var dummy;
  dummy = document.createElement("input");
  // dummy.style.display = 'none'
  document.body.appendChild(dummy);
  dummy.setAttribute("id", `clipboard-${item.id}`);
  document.getElementById(`clipboard-${item.id}`).value = item.value;
  console.log(document.getElementById(`clipboard-${item.id}`).value);
  dummy.select();
  document.execCommand("copy");
  document.body.removeChild(dummy);
  return console.log(`copied ${item.value}`);
};

toggleVisibility = function(e) {
  if (item.is_visible != null) {
    item.is_visible = !item.is_visible;
  } else {
    item.is_visible = true;
  }
  return dispatch("changeVisibility", {
    id: item.id,
    is_visible: item.is_visible
  });
};

go = function() {
  console.log("go");
  console.log(item.value);
  return window.open(item.value, "_blank");
};
</script>
<style lang="sass">section div:first-child {
  padding-left: 0px;
  padding-right: 8px; }

section div {
  padding-left: 8px; }

section div:last-child {
  text-align: right;
  padding: 0px;
  min-width: 125px; }

/*# sourceMappingURL=_KeyPair.svelte.css.map */</style>



<section layout="row">
  <div flex="40">
    <InlineInput value={item.key} on:update={keyChange}/>
  </div>
  <div flex ellipsis>
    {#if item.is_visible}
      <span ellipsis>
      <InlineInput value={item.value} on:update={valueChange} labelClasses="ellipsis"/>
      </span>
    {:else}
      *******************
    {/if}
  </div>
  <div flex="initial">
    <IconButton on:clicked={deleteLine}>
      <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12 19 6.41z"/></svg>
    </IconButton>
    <IconButton on:clicked={copy}>
      <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M16 1H4c-1.1 0-2 .9-2 2v14h2V3h12V1zm-1 4H8c-1.1 0-1.99.9-1.99 2L6 21c0 1.1.89 2 1.99 2H19c1.1 0 2-.9 2-2V11l-6-6zM8 21V7h6v5h5v9H8z"/></svg>
    </IconButton>
    <IconButton on:clicked={toggleVisibility}>
    {#if item.is_visible}
      <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24"><path d="M0 0h24v24H0V0z" fill="none"/><path d="M12 4C7 4 2.73 7.11 1 11.5 2.73 15.89 7 19 12 19s9.27-3.11 11-7.5C21.27 7.11 17 4 12 4zm0 12.5c-2.76 0-5-2.24-5-5s2.24-5 5-5 5 2.24 5 5-2.24 5-5 5zm0-8c-1.66 0-3 1.34-3 3s1.34 3 3 3 3-1.34 3-3-1.34-3-3-3z"/></svg>
    {:else}
      <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24"><path d="M0 0h24v24H0V0zm0 0h24v24H0V0zm0 0h24v24H0V0zm0 0h24v24H0V0z" fill="none"/><path d="M12 6.5c2.76 0 5 2.24 5 5 0 .51-.1 1-.24 1.46l3.06 3.06c1.39-1.23 2.49-2.77 3.18-4.53C21.27 7.11 17 4 12 4c-1.27 0-2.49.2-3.64.57l2.17 2.17c.47-.14.96-.24 1.47-.24zM3.42 2.45L2.01 3.87l2.68 2.68C3.06 7.83 1.77 9.53 1 11.5 2.73 15.89 7 19 12 19c1.52 0 2.97-.3 4.31-.82l3.43 3.43 1.41-1.41L3.42 2.45zM12 16.5c-2.76 0-5-2.24-5-5 0-.77.18-1.5.49-2.14l1.57 1.57c-.03.18-.06.37-.06.57 0 1.66 1.34 3 3 3 .2 0 .38-.03.57-.07L14.14 16c-.65.32-1.37.5-2.14.5zm2.97-5.33c-.15-1.4-1.25-2.49-2.64-2.64l2.64 2.64z"/></svg>
    {/if}
    </IconButton>

    {#if isLink(item.value)}
      <IconButton on:clicked={go}>
        <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24"><path d="M0 0h24v24H0V0z" fill="none"/><path d="M19 19H5V5h7V3H3v18h18v-9h-2v7zM14 3v2h3.59l-9.83 9.83 1.41 1.41L19 6.41V10h2V3h-7z"/></svg>
      </IconButton>

    {/if}
  </div>

</section>
