<script>
  // dependent on Sugar
  // based on https://svelte.dev/repl/72a022c606aa4509abc6b00401538235?version=3.6.7

  import { createEventDispatcher, onMount, afterUpdate } from 'svelte';

	export let name= '';
	export let value= '';
	export let placeholder = '';
	export let required= false;

	export let disabled= false;
  export let defaultvalue;
	// autocomplete props
	export let items = [];
	export let isOpen= false;
	export let results= [];
	export let search= '';

  export let find = "label";
  export let key = "id";

	export let isLoading= false;
	export let arrowCounter= 0;

  let dispatch = createEventDispatcher();

  let className= '';
  let isAsync= false;
  let minChar= 1;
  let maxItems= 10;
  let fromStart= true; // Default type ahead
	let list;
	let input;

  let initialized = false;


  onMount(function () {
    if (items.length > 0) {
      if (!initialized) {
        var initialItem = items.find(function(item){
          return item[key] == defaultvalue
        });
        search = initialItem[find];
        initialized = true;
      }
    }
  })

  /*
  onMount(function () {
    console.log("MOUNT")
    console.log(items);

    var initialItem = items.find(function(item){
      return item[key] == defaultvalue
    });
    console.log(initialItem);
    search = initialItem.label;

  }
  )
  */


  $: { updateDefaultValue(items) };

  function updateDefaultValue (val) {
    //console.log("updateDefaultValue")
    //console.log(items);
    var initialItem = items.find(function(item){
      return item[key] == defaultvalue
    });
    //console.log(initialItem);
    search = initialItem.label;

  }

  //dispatch("changed",defaultvalue);

	async function onChange (event) {
    console.log("onchange");
    //console.log(event);
		// Is the data given by an outside ajax request?

		if (isAsync) {
			isLoading = true;
		} else if (search.length >= Number(minChar)) {
			filterResults()
			isOpen = true;
		}
	}

  function filterResults () {
    console.log(Date.create());
    console.log(`key: ${key}`);
    results = items.filter(function(o){
      console.log(o);
      console.log(search);
      return o[find].toLowerCase().includes(search.toLowerCase().trim())
    });
		const height = results.length > maxItems ? maxItems : results.length
		list.style.height = `${height * 2.25}rem`
  }

  function onKeyDown (event) {
    console.log(items);
    if (event.keyCode === 40 && arrowCounter < results.length) {
      // ArrowDown
      arrowCounter =  arrowCounter + 1
    } else if (event.keyCode === 38 && arrowCounter > 0) {
      // ArrowUp
      arrowCounter =  arrowCounter - 1;
    } else if (event.keyCode === 13) {
      // Enter
      event.preventDefault()
      if (arrowCounter === -1) {
        arrowCounter = 0 // Default select first item of list
      }
      close(arrowCounter)
    } else if (event.keyCode === 27) {
      // Escape
      event.preventDefault()
      close()
    }
  }
  function close (index = -1) {
    isOpen = false;
		arrowCounter = -1;
   	input.blur();
    if (index > -1) {

    	value = results[index][find];
      dispatch("changed",results[index][key]);


    } else if (!value) {
      //search = ''
    }
  }
  function onupdate ({ changed, current }) {
    if (isAsync && changed.items && current.items.length) {
       items = current.items;
       isLoading = false;
       isOpen = true;
       filterResults();
    }
  }
</script>

<style>
  * {
    box-sizing: border-box;
  }

  input {
    font-size: 16px;
    padding: 0.4em;
  }

  .autocomplete {
    position: relative;
  }

  .hide-results {
    display: none;
  }

  .autocomplete-results {
    padding: 0;
    margin: 0;
    border: 1px solid #dbdbdb;
    height: 6rem;
    overflow: auto;
    width: 400px;

    background-color: white;
    box-shadow: 2px 2px 24px rgba(0, 0, 0, 0.1);
    position: absolute;
    z-index: 100;
  }

  .autocomplete-result {
    color: #7a7a7a;
    list-style: none;
    text-align: left;
    height: 2rem;
    padding: 0.25rem 0.5rem;
    cursor: pointer;
  }

  .autocomplete-result > :global(span) {
    background-color: none;
    color: #242424;
    font-weight: bold;
  }

  .autocomplete-result.is-active,
  .autocomplete-result:hover {
    background-color: #dbdbdb;
  }
</style>
<svelte:window on:click="{()=>close()}" />
<div on:click="{(event)=>event.stopPropagation()}" class="autocomplete">
  <input
    type="text"
    class="{className}"
    {name}
    {placeholder}
    {required}
    {disabled}
    autocomplete={name}
    bind:value={search}
    on:input="{(event)=>onChange(event)}"
    on:focus
    on:blur
    on:keydown="{(event)=>onKeyDown(event)}"
  >

  <ul class="autocomplete-results{!isOpen ? ' hide-results' : ''}" bind:this={list}>
		{#each results as result, i}
				<li on:click="{()=>close(i)}" class="autocomplete-result{ i === arrowCounter ? ' is-active' : '' }">
				{@html result[find]}
				</li>
		{/each}
  </ul>

</div>
