<script lang="coffeescript">var add, click, debouncedRefresh, menuClose, menustate, primaryFocus, refresh, update;

import {
  location,
  replace
} from 'svelte-spa-router';

import IconButton from '../../components/common/IconButton.svelte';

import {
  databaseLoaded,
  collection
} from "../../helpers/stores.js";

import OverflowMenu from "../../components/carbon/OverflowMenu/OverflowMenu.svelte";

import OverflowMenuItem from '../../components/carbon/OverflowMenu/OverflowMenuItem.svelte';

import Search from "../../components/carbon/Search/Search.svelte";

menustate = false;

click = function(target) {
  console.log("location", location);
  return replace(`/${target}`);
};

add = function(type) {
  var cb;
  cb = function(data) {
    return menustate = false;
  };
  return collection.addStub(type, null, cb);
};

refresh = function() {
  return collection.refresh();
};

debouncedRefresh = refresh.debounce(100);

update = function() {
  return debouncedRefresh();
};

primaryFocus = true;

menuClose = function(e) {
  console.log(e.detail.text);
  return add(e.detail.text);
};
</script>

