<script lang="coffeescript">export let item;
var debouncedUpdate, editorIsVisible, element, handleBlur, html, inlineEdit, inlineEditor, saveCb, showEditor, update;

import uuid from 'uuid';

import {
  onMount
} from 'svelte';

element = uuid();

import Editor from 'cl-editor/dist';

import {
  databaseLoaded,
  collection
} from "../../../helpers/stores.js";

inlineEdit = null;

inlineEditor = null;

html = item.data.contents;

saveCb = function(r) {};

update = function(html) {
  console.log("saved");
  return collection.save({
    id: item.id,
    data: {
      contents: html
    }
  }, saveCb);
};

debouncedUpdate = update.debounce(500);

editorIsVisible = false;

handleBlur = function(e) {
  if (editorIsVisible) {
    return console.log(e);
  }
};

showEditor = function(e) {
  console.log(`editorIsVisible: ${editorIsVisible}`);
  if (!editorIsVisible) {
    editorIsVisible = true;
    console.log("showEditor");
    console.log(e.target);
    inlineEditor = new Editor({
      target: document.getElementById(`${element}-editor`),
      data: {
        actions: [
          'h1',
          'h2',
          'p',
          'b',
          'i',
          'u',
          'strike',
          'blockquote',
          'ol',
          'ul',
          'a',
          {
            name: 'codeblock',
            icon: '{ }',
            title: 'Code Block',
            result: function() {
              return document.execCommand('formatBlock',
          false,
          '<PRE>');
            }
          },
          'removeFormat'
        ],
        height: 'auto',
        html: `${item.data.contents}`
      }
    });
    inlineEditor.on('blur', function() {
      editorIsVisible = false;
      item.data.contents = inlineEditor.getHtml();
      update(item.data.contents);
      return inlineEditor.destroy();
    });
    return inlineEditor.on('change', function() {
      item.data.contents = inlineEditor.getHtml();
      return debouncedUpdate(item.data.contents);
    });
  }
};
</script>
<style lang="sass">[hidden="true"] {
  display: none; }

[preview] {
  border-radius: 4px;
  width: 100%;
  min-height: 14px; }
  [preview]:hover {
    background: #eeeeee; }

/*# sourceMappingURL=Note.svelte.css.map */</style>

<div id="{element}" preview on:click={showEditor} hidden={editorIsVisible} class="cl-preview">{@html (item.data.contents || "This note is empty, click to edit")}</div>
<div id="{element}-editor"></div>
