<script>
import { tick, createEventDispatcher } from 'svelte';

let dispatch = createEventDispatcher();
// Props
export let value = '';
export let type = 'text';
export let placeholder = '';
export let labelClasses = '';
export let inputClasses = '';
let editing = false;
let inputEl;
let label;
// Computed
$: isText = type === 'text';
$: isNumber = type === 'number';
$: if (isNumber) {
      label = value === '' ? placeholder : value;
    } else if (isText) {
      label = value ? value : placeholder;
    }
const toggle = async (_) => {
  editing = !editing;
  if (editing) {
    await tick();
    inputEl.focus();
  }
};
const handleInput = (e) => {
  value = isNumber ? +e.target.value : e.target.value;
};
const handleKeyup = (e) => {
  dispatch("keyup", value);
  if (e.keyCode === 13) inputEl.blur();
};
const handleBlur = (_) => {
  toggle();
  dispatch("update",value);
};
</script>
<style lang="sass">input, div {
  width: 100%; }
  input:hover, div:hover {
    background: #eeeeee; }

div {
  min-height: 24px; }

.strikethrough {
  text-decoration: line-through; }

/*# sourceMappingURL=InlineInput.svelte.css.map */</style>
{#if editing && (isText || isNumber)}
  <input
    class={inputClasses}
    bind:this={inputEl}
    {type}
    {value}
    {placeholder}
    on:input={handleInput}
    on:keyup={handleKeyup}
    on:blur={handleBlur}>
{:else}
  <div
    class={labelClasses}
    on:click={toggle}>
  {label}
  </div>
{/if}
