<script lang="coffeescript">export let expression;
var info, math, parser, result, type;

import {
  create,
  all
} from 'mathjs';

import IconIndicator from "../../../components/common/IconIndicator.svelte";

math = create(all, {
  number: 'number'
});

type = "Math";

parser = math.parser();

result = function(expr) {
  var dateErr, dateattempt, mathErr, output;
  output = "";
  try {
    output = math.format(parser.evaluate((expr || "").trim()), {
      precision: 14
    });
    return type = math.typeOf(parser.evaluate(expr || "")).titleize();
  } catch (error) {
    mathErr = error;
    try {
      // console.log "Math error:"
      // console.log mathErr
      dateattempt = Date.create(expr);
      if (dateattempt.isValid()) {
        type = "Date";
        return output = dateattempt.format();
      } else {
        type = "Unknown";
        return output = "I don't understand this expression yet.";
      }
    } catch (error) {
      dateErr = error;
    }
  } finally {
    // console.log "Date error:"
    // console.log dateErr
    if (output === "undefined") {
      if (expr.length === 0) {
        output = "";
      } else {
        output = "I don't understand this expression yet.";
      }
      type = "Unknown";
    }
    return output || "";
  }
};

info = function() {
  var err, output;
  output = null;
  try {
    if (math.format(parser.evaluate((expression || "").trim())).length - (math.format(parser.evaluate((expression || "").trim()), {
      precision: 14
    }).length) > 4) {
      return output = `This floating point result contains a round-off error. The raw result is: ${math.format(parser.evaluate((expression || "").trim()))}`;
    }
  } catch (error) {
    err = error;
  } finally {
    // console.log err
    return output;
  }
};

$: displayResult = result(expression);
</script>
