    background: #c92a2a; }
  label.Unit {
    background: #e8590c; }
  label.Complex, label.Fraction, label.Array, label.Matrix {
    background: #7048e8; }

/*# sourceMappingURL=__Expression.svelte.css.map */</style>
<div>
  <label class={type}>{type}</label>
  <span>{displayResult}</span>
  {#if info() != null}
    <IconIndicator popoverText={info()}>
      <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm1 15h-2v-6h2v6zm0-8h-2V7h2v2z"/></svg>
    </IconIndicator>
  {/if}
</div>
