<script lang="coffeescript">export let item;
var add, changeExpression, checkKeys, deleteLine, expressionElementId, mounted, newItem, placeholder, reindex, reorder, reset, saveCb, update;

import uuid from 'uuid';

import {
  tick,
  createEventDispatcher,
  onMount
} from 'svelte';

import {
  databaseLoaded,
  collection
} from "../../../helpers/stores.js";

import Solver from "./_Solver.svelte";

import Expression from "./__Expression.svelte";

import DraggableList from "../../../components/common/DraggableList.svelte";

mounted = false;

onMount(function() {
  return mounted = true;
});

expressionElementId = `new-solver-${item.id}`;

newItem = null;

saveCb = function(r) {
  return reset(true);
};

update = function() {
  reindex();
  return collection.save({
    id: item.id,
    data: {
      lines: item.data.lines
    }
  }, saveCb);
};

add = function() {
  // set ordinal to max + 1
  newItem.created_at = Date.create();
  newItem.modified_at = newItem.created_at;
  item.data.lines.push(newItem);
  return update();
};

reset = function(saved) {
  return newItem = {
    id: uuid(),
    ordinal: item.data.lines.length,
    expression: "",
    created_at: null,
    modified_at: null
  };
};

reset();

changeExpression = function(e) {
  var itemToChangeIndex;
  itemToChangeIndex = item.data.lines.findIndex(function(o) {
    return o.id === e.detail.id;
  });
  item.data.lines[itemToChangeIndex].expression = e.detail.expression;
  return update();
};

placeholder = function() {
  return ["e.g., 5 feet in centimeters", "e.g., 50 * 20 * 1.15", "e.g., (365 * 24) + 1", "e.g., 12 lbs in kg", "e.g., 30 days from now", "e.g., Next year", "e.g., 80 days ago", "e.g., 5 feet in centimeters", "e.g., (10.00 + 23.88 + 12.00) * 0.85 * 1.13"].sample();
};

reindex = function() {
  return item.data.lines.forEach(function(item, index) {
    return item.ordinal = index;
  });
};

reorder = function(ev) {
  item.data.lines = ev.detail;
  return update();
};

deleteLine = function(e) {
  item.data.lines.remove(function(o) {
    return o.id === e.detail;
  });
  // todo: reindex ordinals
  return update();
};

checkKeys = function(e) {
  if ((e.code === "NumpadEnter") || ((e.code === "Enter") && !e.shiftKey)) {
    if (newItem.expression.length > 0) {
      return add();
    }
  }
};
</script>
<style lang="sass">footer div:first-child {
  padding-right: 8px; }

footer div:last-child {
  padding-left: 8px; }

input {
  width: 100%; }

/*# sourceMappingURL=Solver.svelte.css.map */</style>




<section>
    {#if mounted}
    <DraggableList
        list={item.data.lines}
        key="id"
        on:sort={reorder}
        let:item
    		let:index
    >
        <Solver {item} {index} on:changeExpression={changeExpression} on:delete={deleteLine}/>
    </DraggableList>
    {/if}


</section>
<footer layout="row">
  <div flex="40">
    <input id={expressionElementId} type="text" bind:value={newItem.expression} on:keyup={checkKeys} placeholder={placeholder()}/>
  </div>
  <div flex="60">
    <Expression expression={newItem.expression}/>
  </div>
</footer>
