<script lang="coffeescript">var current, currentCalendar, currentCalendarHeader, currentDate, currentMonth, generate, next, nextYear, previous, previousYear, weeks;

import Sugar from "sugar";

Sugar.extend();

export let relativeDate;

weeks = [];

currentCalendar = [];

currentMonth = null;

currentCalendarHeader = null;

currentDate = null;

generate = function() {
  var calendarEnd, calendarStart, count, currentWeek, results, runningDate;
  weeks = [];
  if (currentDate != null) {
    currentCalendarHeader = currentDate.format("{Month} {yyyy}");
    currentMonth = currentDate.format("{M}");
    calendarStart = currentDate.clone().beginningOfMonth().beginningOfWeek();
    calendarEnd = currentDate.clone().endOfMonth().endOfWeek();
    runningDate = calendarStart.clone();
    currentWeek = [];
    count = 0;
    results = [];
    while (runningDate.isBetween(calendarStart, calendarEnd)) {
      currentWeek.push(runningDate.clone());
      runningDate.addDays(1);
      count++;
      if (count === 7) {
        weeks.push(currentWeek);
        currentWeek = [];
        results.push(count = 0);
      } else {
        results.push(void 0);
      }
    }
    return results;
  }
};

current = function() {
  currentDate = Date.create().clone();
  console.log(currentDate);
  return generate();
};

next = function() {
  currentDate.addMonths(1);
  generate();
  return console.log(`currentDate ${currentDate}`);
};

previous = function() {
  currentDate.addMonths(-1);
  generate();
  return console.log(`currentDate ${currentDate}`);
};

nextYear = function() {
  currentDate.addYears(1);
  generate();
  return console.log(`currentDate ${currentDate}`);
};

previousYear = function() {
  currentDate.addYears(-1);
  generate();
  return console.log(`currentDate ${currentDate}`);
};

current();
</script>

