<script lang="coffeescript">export let item;
var add, changeDescription, changeIsComplete, checkKeys, deleteLine, mounted, newItem, reindex, reorder, reset, saveCb, update;

import uuid from 'uuid';

import {
  tick,
  createEventDispatcher,
  onMount
} from 'svelte';

import {
  databaseLoaded,
  collection
} from "../../../helpers/stores.js";

import ChecklistItem from "./_ChecklistItem.svelte";

import DraggableList from "../../../components/common/DraggableList.svelte";

newItem = null;

saveCb = function(r) {
  return reset();
};

update = function() {
  reindex();
  return collection.save({
    id: item.id,
    data: {
      lines: item.data.lines
    }
  }, saveCb);
};

add = function() {
  // set ordinal to max + 1
  newItem.created_at = Date.create();
  newItem.modified_at = newItem.created_at;
  item.data.lines.push(newItem);
  return update();
};

reset = function() {
  return newItem = {
    id: uuid(),
    ordinal: item.data.lines.length,
    is_complete: false,
    description: "",
    due_date: null,
    created_at: null,
    modified_at: null,
    who: null
  };
};

reset();

changeDescription = function(e) {
  var itemToChangeIndex;
  itemToChangeIndex = item.data.lines.findIndex(function(o) {
    return o.id === e.detail.id;
  });
  item.data.lines[itemToChangeIndex].description = e.detail;
  return update();
};

changeIsComplete = function(e) {
  var itemToChangeIndex;
  itemToChangeIndex = item.data.lines.findIndex(function(o) {
    return o.id === e.detail.id;
  });
  item.data.lines[itemToChangeIndex].is_complete = e.detail.is_complete;
  return update();
};

reindex = function() {
  return item.data.lines.forEach(function(item, index) {
    return item.ordinal = index;
  });
};

reorder = function(ev) {
  item.data.lines = ev.detail;
  return update();
};

deleteLine = function(e) {
  item.data.lines.remove(function(o) {
    return o.id === e.detail;
  });
  // todo: reindex ordinals
  return update();
};

checkKeys = function(e) {
  if ((e.code === "NumpadEnter") || ((e.code === "Enter") && !e.shiftKey)) {
    return add();
  }
};

$: completed = item.data.lines.count(function(item){ return item.is_complete});

mounted = false;

onMount(function() {
  return mounted = true;
});
</script>
<style lang="sass">header {
  margin-bottom: 18px; }

input {
  width: 100%; }

/*# sourceMappingURL=Checklist.svelte.css.map */</style>

<div>
  <header>
    <input type="text" bind:value={newItem.description} on:keyup={checkKeys} placeholder="What needs to be done?"/>
  </header>
  <section>
    {#if mounted}
    <DraggableList
        list={item.data.lines}
        key="id"
        on:sort={reorder}
        let:item
    		let:index
    >
        <ChecklistItem {item} {index} on:changeDescription={changeDescription} on:changeIsComplete={changeIsComplete} on:delete={deleteLine}/>
    </DraggableList>
    {/if}

  </section>
  <footer>
    {completed} / {item.data.lines.length} completed.
  </footer>
</div>
