<script lang="coffeescript">var copy, deleteLine, dispatch, evaluateExpression, expressionChange, expressionKeyup, math, result;

import {
  tick,
  createEventDispatcher
} from 'svelte';

import {
  create,
  all
} from 'mathjs';

math = create(all, {
  number: 'number'
});

dispatch = createEventDispatcher();

export let item;

export let index;

import InlineInput from "../../../components/common/InlineInput.svelte";

import IconButton from "../../../components/common/IconButton.svelte";

import Expression from "./__Expression.svelte";

result = null;

expressionKeyup = function(e) {
  return item.expression = e.detail;
};

expressionChange = function(e) {
  dispatch("changeExpression", {
    id: item.id,
    expression: e.detail
  });
  return item.expression = e.detail;
};

deleteLine = function(e) {
  return dispatch("delete", item.id);
};

copy = function(e) {
  var dummy;
  dummy = document.createElement("input");
  document.body.appendChild(dummy);
  dummy.setAttribute("id", `clipboard-${item.id}`);
  document.getElementById(`clipboard-${item.id}`).value = evaluateExpression(item.expression);
  dummy.select();
  document.execCommand("copy");
  return document.body.removeChild(dummy);
};

evaluateExpression = function(expression) {
  var dateattempt, e, e2, invalidExpression;
  invalidExpression = "";
  if (expression != null) {
    if (expression.trim().length > 0) {
      try {
        return math.evaluate(expression);
      } catch (error) {
        e = error;
        try {
          //console.log e
          dateattempt = Date.create(expression);
          if (dateattempt.isValid()) {
            return dateattempt;
          } else {
            return invalidExpression;
          }
        } catch (error) {
          e2 = error;
          //console.log e2
          return invalidExpression;
        }
      }
    } else {
      return "";
    }
  } else {
    return "";
  }
};
</script>
<style lang="sass">section div:first-child {
  padding-left: 0px;
  padding-right: 8px; }

section div {
  padding-left: 8px; }

section div:last-child {
  text-align: right;
  padding: 0px;
  min-width: 80px; }

/*# sourceMappingURL=_Solver.svelte.css.map */</style>



<section layout="row">
  <div flex="40">
    <InlineInput value={item.expression} on:update={expressionChange} on:keyup={expressionKeyup}/>
  </div>
  <div flex>
    <Expression expression={item.expression}/>
    <div>

    </div>
  </div>
  <div flex="initial">
    <IconButton on:clicked={deleteLine}>
      <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12 19 6.41z"/></svg>
    </IconButton>
    <IconButton on:clicked={copy}>
      <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M16 1H4c-1.1 0-2 .9-2 2v14h2V3h12V1zm-1 4H8c-1.1 0-1.99.9-1.99 2L6 21c0 1.1.89 2 1.99 2H19c1.1 0 2-.9 2-2V11l-6-6zM8 21V7h6v5h5v9H8z"/></svg>
    </IconButton>
  </div>

</section>
