<script lang="coffeescript">export let item;
var add, changeKey, changeValue, changeVisibility, checkKeys, deleteLine, keypairElementId, mounted, newItem, reindex, reorder, reset, saveCb, update;

import uuid from 'uuid';

import {
  tick,
  createEventDispatcher,
  onMount
} from 'svelte';

import {
  databaseLoaded,
  collection
} from "../../../helpers/stores.js";

import KeyPair from "./_KeyPair.svelte";

import DraggableList from "../../../components/common/DraggableList.svelte";

mounted = false;

onMount(function() {
  return mounted = true;
});

keypairElementId = `new-keypair-${item.id}`;

newItem = null;

saveCb = function(r) {
  console.log("saved!");
  console.log(r);
  return reset(true);
};

update = function() {
  reindex();
  return collection.save({
    id: item.id,
    data: {
      lines: item.data.lines
    }
  }, saveCb);
};

add = function() {
  console.log("add");
  console.log(newItem);
  // set ordinal to max + 1
  newItem.created_at = Date.create();
  newItem.modified_at = newItem.created_at;
  item.data.lines.push(newItem);
  return update();
};

reset = function(saved) {
  newItem = {
    id: uuid(),
    ordinal: item.data.lines.length,
    key: "",
    value: "",
    is_visible: true,
    created_at: null,
    modified_at: null
  };
  if (saved == null) {
    if (document.getElementById(keypairElementId) != null) {
      // document.getElementById(keypairElementId).focus()
      return 1;
    }
  }
};

reset();

changeKey = function(e) {
  var itemToChangeIndex;
  console.log("changeKey");
  console.log(e.detail);
  itemToChangeIndex = item.data.lines.findIndex(function(o) {
    return o.id === e.detail.id;
  });
  console.log(item.data.lines[itemToChangeIndex]);
  item.data.lines[itemToChangeIndex].key = e.detail.key;
  return update();
};

changeValue = function(e) {
  var itemToChangeIndex;
  console.log("changeValue");
  console.log(e.detail);
  itemToChangeIndex = item.data.lines.findIndex(function(o) {
    return o.id === e.detail.id;
  });
  console.log(item.data.lines[itemToChangeIndex]);
  item.data.lines[itemToChangeIndex].value = e.detail.value;
  return update();
};

changeVisibility = function(e) {
  var itemToChangeIndex;
  console.log("changeVisibility");
  console.log(e.detail);
  itemToChangeIndex = item.data.lines.findIndex(function(o) {
    return o.id === e.detail.id;
  });
  console.log(item.data.lines[itemToChangeIndex]);
  item.data.lines[itemToChangeIndex].is_visible = e.detail.is_visible;
  return update();
};

reindex = function() {
  return item.data.lines.forEach(function(item, index) {
    return item.ordinal = index;
  });
};

reorder = function(ev) {
  item.data.lines = ev.detail;
  return update();
};

deleteLine = function(e) {
  item.data.lines.remove(function(o) {
    return o.id === e.detail;
  });
  // todo: reindex ordinals
  return update();
};

checkKeys = function(e) {
  console.log(e);
  if ((e.code === "NumpadEnter") || ((e.code === "Enter") && !e.shiftKey)) {
    return add();
  }
};
</script>
<style lang="sass">header {
  margin-bottom: 18px; }
  header div:first-child {
    padding-right: 8px; }
  header div:last-child {
    padding-left: 8px; }

input {
  width: 100%; }

/*# sourceMappingURL=KeyPair.svelte.css.map */</style>

<header layout="row">
  <div flex="40">
    <input id={keypairElementId} type="text" bind:value={newItem.key} on:keyup={checkKeys} placeholder="Label"/>
  </div>
  <div flex="60">
    <input type="text" bind:value={newItem.value} on:keyup={checkKeys} placeholder="Data"/>
  </div>
</header>




<section>
    {#if mounted}
    <DraggableList
        list={item.data.lines}
        key="id"
        on:sort={reorder}
        let:item
    		let:index
    >
        <KeyPair {item} {index} on:changeKey={changeKey} on:changeValue={changeValue} on:changeVisibility={changeVisibility} on:delete={deleteLine}/>
    </DraggableList>
    {/if}



</section>
