<script lang="coffeescript">export let item;
var calendarComponentRef, calendarWeekends, eventClick, eventDrop, eventResize, events, gotoPast, handleDateClick, plugins, saveCb, toggleWeekends, update;

import uuid from 'uuid';

import {
  databaseLoaded,
  collection
} from "../../../helpers/stores.js";

import FullCalendar from '../../../components/common/FullCalendar.svelte';

import dayGridPlugin from '@fullcalendar/daygrid';

import timeGridPlugin from '@fullcalendar/timegrid';

import listPlugin from '@fullcalendar/list';

import interactionPlugin from '@fullcalendar/interaction';

plugins = [dayGridPlugin, timeGridPlugin, interactionPlugin];

calendarComponentRef = null;

calendarWeekends = true;

//console.log JSON.stringify(item, null, 2)
events = item.data.events;

toggleWeekends = function() {
  calendarWeekends = !calendarWeekends;
};

gotoPast = function() {
  var calendarApi;
  calendarApi = calendarComponentRef.getAPI();
  calendarApi.gotoDate('2000-01-01');
};

// call a method on the Calendar object
saveCb = function(r) {
  console.log('saved!');
  console.log(r);
};

update = function() {
  collection.save({
    id: item.id,
    data: {
      events: events
    }
  }, saveCb);
};

handleDateClick = function(e) {
  var arg, argDate;
  arg = e.detail;
  console.log("-------------");
  console.log(arg);
  console.log("-------------");
  argDate = Date.create(argDate);
  events = [
    ...events,
    {
      id: uuid(),
      title: "New Entry",
      start: arg.date,
      end: arg.date.clone().set({
        hour: 17,
        minute: 0,
        second: 0
      }),
      allDay: false,
      editable: true,
      startEditable: true,
      durationEditable: true,
      overlap: false
    }
  ];
  console.log(events);
  return update();
};

eventClick = function(e) {
  console.log("eventClick");
  console.log("e");
  return console.log(e.detail);
};

eventDrop = function(e) {
  console.log("eventDrop");
  console.log("e");
  return console.log(e.detail);
};

eventResize = function(e) {
  var index;
  console.log("eventResize");
  console.log("e");
  console.log(e.detail.event);
  index = events.findIndex(function(o) {
    return o.id === e.detail.event.id;
  });
  events[index].start = e.detail.event.start;
  events[index].end = e.detail.event.end;
  return update();
};
</script>

<style>

	.demo-app {
		font-family: Arial, Helvetica Neue, Helvetica, sans-serif;
		font-size: 14px;
	}

	.demo-app-top {
		margin: 0 0 3em;
	}

	.demo-app-calendar {
		margin: 0 auto;
		max-width: 900px;
	}
</style>

<div class="demo-app">
	<div class="demo-app-top">
		<button on:click={toggleWeekends}>toggle weekends</button>
		&nbsp;
		<button on:click={gotoPast}>go to a date in the past</button>
		&nbsp; (also, click a date/time to add an event)
	</div>
	<div class="demo-app-calendar">
		<FullCalendar
			bind:this={calendarComponentRef}
			defaultView="timeGridWeek"
			{events}
			header={{ left: 'prev,next today', center: 'title', right: 'dayGridMonth,timeGridWeek,timeGridDay,listWeek' }}
			height={800}
			on:dateClick={handleDateClick}
      on:eventClick={eventClick}
      on:eventDrop={eventResize}
      on:eventResize={eventResize}
			plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin, listPlugin]}
			weekends={calendarWeekends} />
	</div>
</div>
