<script lang="coffeescript">export let item;
var cancel, debouncedUpdate, deleteItem, deleteModal, favorite, jsonModal, lock, menuClose, nameChange, primaryFocus, saveCb, test, update;

import {
  databaseLoaded,
  collection
} from "../../helpers/stores.js";

import InlineInput from "../../components/common/InlineInput.svelte";

import IconButton from '../../components/common/IconButton.svelte';

import OverflowMenu from "../../components/carbon/OverflowMenu/OverflowMenu.svelte";

import OverflowMenuItem from '../../components/carbon/OverflowMenu/OverflowMenuItem.svelte';

import Note from './items/Note.svelte';

import Checklist from './items/Checklist.svelte';

import KeyPair from './items/KeyPair.svelte';

import Timesheet from './items/Timesheet.svelte';

import Solver from './items/Solver.svelte';

import Code from './items/Code.svelte';

import Markdown from './items/Markdown.svelte';

import Modal from '../../components/common/ModalGeneric.svelte';

import ModalFooterDelete from '../../components/common/ModalFooterDelete.svelte';

import ModalFooterCancel from '../../components/common/ModalFooterCancel.svelte';

deleteModal = false;

jsonModal = false;

saveCb = function(returnedObject) {
  if (returnedObject != null) {
    console.log("returnedObject");
    console.log(returnedObject);
    console.log("saved!");
    Object.merge(item, returnedObject, {
      deep: true
    });
    return console.log(item);
  } else {
    return console.log("Save error");
  }
};

update = function(name) {
  return collection.save({
    id: item.id,
    name: name
  }, saveCb);
};

favorite = function() {
  return collection.save({
    id: item.id,
    is_favorite: item.is_favorite
  }, saveCb);
};

lock = function() {
  return collection.save({
    id: item.id,
    is_locked: item.is_locked
  }, saveCb);
};

debouncedUpdate = update.debounce(250);

deleteItem = function() {
  console.log("Delete confirmed");
  return collection.delete(item, cancel);
};

cancel = function() {
  deleteModal = false;
  return jsonModal = false;
};

nameChange = function(e) {
  update(e.detail);
  return item.name = e.detail;
};

test = function() {
  return console.log(item);
};

menuClose = function(e) {
  var cb, newItem;
  switch (e.detail.text) {
    case "toggle_favorite":
      item.is_favorite = !item.is_favorite;
      return favorite();
    case "toggle_lock":
      item.is_locked = !item.is_locked;
      return lock();
    case "json":
      return jsonModal = true;
    case "clone":
      cb = function(data) {};
      newItem = Object.clone(item, true);
      newItem.name = `${item.name} clone`;
      delete newItem.id;
      return collection.addStub(item.type, newItem, cb);
    case "delete":
      return deleteModal = true;
  }
};

primaryFocus = true;
</script>

<style lang="sass">header {
  margin-bottom: 26px; }

header > div {
  width: 100%; }

article {
  padding: 36px; }

footer {
  font-size: 10px;
  padding-top: 18px; }
  footer div {
    display: inline-block;
    margin-right: 18px; }

.actions {
  width: 32px;
  min-width: 32px; }

label {
  margin-left: 16px; }

textarea[json] {
  width: 100%;
  height: 100%; }

/*# sourceMappingURL=ListItem.svelte.css.map */</style>


<article>
  <header layout="row">
    <div flex>
      <h1><InlineInput value={item.name} on:update={nameChange}/></h1>
    </div>
    <div  a="r" class="actions" flex="initial">
      <OverflowMenu on:close={menuClose} flipped={true}>
          <OverflowMenuItem text="toggle_favorite" bind:primaryFocus >
            <input type="checkbox" bind:checked={item.is_favorite} hidden>
            {#if item.is_favorite}
              <svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" width="24px"
                 height="24px" viewBox="0 0 24 24" enable-background="new 0 0 24 24" xml:space="preserve">
              <g id="Bounding_Boxes">
              	<path fill="none" d="M0,0h24v24H0V0z"/>
              	<path fill="none" d="M0,0h24v24H0V0z"/>
              </g>
              <g id="Sharp">
                <path d="M12,17.27L18.18,21l-1.64-7.03L22,9.24l-7.19-0.61L12,2L9.19,8.63L2,9.24l5.46,4.73L5.82,21L12,17.27z"/>
              </g>
              </svg>
            {:else}
              <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M22 9.24l-7.19-.62L12 2 9.19 8.63 2 9.24l5.46 4.73L5.82 21 12 17.27 18.18 21l-1.63-7.03L22 9.24zM12 15.4l-3.76 2.27 1-4.28-3.32-2.88 4.38-.38L12 6.1l1.71 4.04 4.38.38-3.32 2.88 1 4.28L12 15.4z"/></svg>
            {/if}
            <label>
              {#if item.is_favorite}
                Unfavorite
              {:else}
                Favorite
              {/if}
            </label>
          </OverflowMenuItem>
          <OverflowMenuItem text="toggle_lock">
            <input type="checkbox" bind:checked={item.is_locked} hidden>
            {#if item.is_locked}
              <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24"><g fill="none"><path d="M0 0h24v24H0V0z"/><path d="M0 0h24v24H0V0z" opacity=".87"/></g><path d="M20 8h-3V6.21c0-2.61-1.91-4.94-4.51-5.19C9.51.74 7 3.08 7 6v2H4v14h16V8zm-8 9c-1.1 0-2-.9-2-2s.9-2 2-2 2 .9 2 2-.9 2-2 2zM9 8V6c0-1.66 1.34-3 3-3s3 1.34 3 3v2H9z"/></svg>
            {:else}
              <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24"><path d="M0 0h24v24H0V0z" fill="none"/><path d="M20 8h-3V6.21c0-2.61-1.91-4.94-4.51-5.19C9.51.74 7 3.08 7 6h2c0-1.13.6-2.24 1.64-2.7C12.85 2.31 15 3.9 15 6v2H4v14h16V8zm-2 12H6V10h12v10zm-6-3c1.1 0 2-.9 2-2s-.9-2-2-2-2 .9-2 2 .9 2 2 2z"/></svg>
            {/if}
            <label>
              {#if item.is_locked}
                Unlock
              {:else}
                Lock
              {/if}
            </label>
          </OverflowMenuItem>

          <OverflowMenuItem text="clone">
            <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24"><path d="M0 0h24v24H0V0z" fill="none"/><path d="M16 8h-2v3h-3v2h3v3h2v-3h3v-2h-3V8zM2 12c0-2.79 1.64-5.2 4.01-6.32V3.52C2.52 4.76 0 8.09 0 12s2.52 7.24 6.01 8.48v-2.16C3.64 17.2 2 14.79 2 12zm13-9c-4.96 0-9 4.04-9 9s4.04 9 9 9 9-4.04 9-9-4.04-9-9-9zm0 16c-3.86 0-7-3.14-7-7s3.14-7 7-7 7 3.14 7 7-3.14 7-7 7z"/></svg>
            <label>
              Clone
            </label>
          </OverflowMenuItem>
          <OverflowMenuItem text="json">
            <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24"><path d="M0 0h24v24H0V0z" fill="none"/><path d="M9.4 16.6L4.8 12l4.6-4.6L8 6l-6 6 6 6 1.4-1.4zm5.2 0l4.6-4.6-4.6-4.6L16 6l6 6-6 6-1.4-1.4z"/></svg>
            <label>
              Copy as JSON
            </label>
          </OverflowMenuItem>
          <OverflowMenuItem text="delete" disabled={item.is_locked}>


              <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M6 21h12V7H6v14zM19 4h-3.5l-1-1h-5l-1 1H5v2h14V4z"/></svg>

              <label>
                Delete
              </label>


          </OverflowMenuItem>
      </OverflowMenu>
    </div>


  </header>
  <section>
      {#if item.type== "note"}
        <Note {item}/>
      {/if}
      {#if item.type== "code"}
        <Code {item}/>
      {/if}
      {#if item.type== "checklist"}
        <Checklist {item}/>
      {/if}
      {#if item.type== "solver"}
        <Solver {item}/>
      {/if}
      {#if item.type== "timesheet"}
        <Timesheet {item}/>
      {/if}
      {#if item.type== "list"}
        <KeyPair {item}/>
      {/if}
      {#if item.type== "markdown"}
        <Markdown {item}/>
      {/if}
  </section>
  <footer>
    <div>Created: {item.created_at.format("")}</div>
    <div>Modified: {item.modified_at.format("")}</div>
  </footer>
</article>
{#if deleteModal}
<Modal>
  <h1 slot="header" fg="darkest-red">Delete this item?</h1>
  <p>
  This item will be permanently deleted.
  </p>

  <div slot="footer">
  <ModalFooterDelete on:confirm-delete={deleteItem} on:cancel={cancel}/>
  </div>
</Modal>
{/if}
{#if jsonModal}
<Modal>
  <h1 slot="header">JSON</h1>
  <textarea json>
    {JSON.stringify(item, null, 2)}
  </textarea>

  <div slot="footer">
  <ModalFooterCancel on:cancel={cancel}/>
  </div>
</Modal>
{/if}
